@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
    .icon__wrapper{
        @apply border border-gray-500 rounded-full w-[40px] h-[40px] 
        place-items-center grid text-[28px]
    }
    .feature_btn{
        @apply bg-accent text-white px-4 py-1 rounded-full hover:bg-accentDark;
    }
    body {
        background-color: #ebebe0;
    }
}